<template>
  <div id="get-todo" class="container">
    <!-- <input class="form-control" :value="newTodo" @change="getTodo" placeholder="I need to ..." @keyup.enter="addTodo" /> -->
    <input class="form-control" v-model="msg" placeholder="I need to ..." @keyup.enter="addTodo" />
    <button class="btn btn-primary" @click="addTodo">
      <i class="iconfont icon-add"></i> ADD
    </button>
  </div>
</template>
<script>
  export default {
    methods: {
      getTodo(e) {
        var str = e.target.value.trim();
        this.$store.dispatch('getTodo', str)
      },
      addTodo() {
        if(this.$store.state.newTodo) {
          this.$store.dispatch('addTodo');
          this.$store.dispatch('clearTodo');
        }
      }
    },
    computed: {
      newTodo() {
        return this.$store.getters.newTodo
      },
      msg: {
        get() {
          return this.$store.getters.newTodo
        },
        set(value) {
          this.$store.dispatch('getTodo', value.trim())
        }
      }
    }
  }
</script>
<style>
  .btn-primary {
    background: #66ccff;
    color: #fff;
  }
</style>
